.cv-logo_box{
  display: inline-block;
  width: 80%;
  padding-left: 8px;
  vertical-align: middle;
}
.cv-text_p{
  font-size: 15px;
  padding-top: 10px;
  text-indent: 10px;
}
.cv-ul{
  list-style: none;
  padding: 5px 15px 15px;
  margin: 0;
}
.cv-ul li{
	background-color: #fff;
  font-weight: 600;
  font-size: 14px;
  outline: 1px solid rgb(248, 249, 250);
  outline-offset: -2px;
  padding: 8px;
  box-shadow: inset 0px -12px 6px 0px #e4e4e4;
}
.cv-ul li:hover{
  z-index: 10;
}
.cv-ul__b{
  display: block;
  font-size: 12px;
  border-left: 1px solid rgb(223 223 223);
  padding-left: 8px;
}
.cv-ul__b2{
  display: block;
  font-size: 11px;
  padding-left: 0px;
  font-weight: 700;
  text-align: center;
}
.cv-ul__span{
  font-size: 14px;
}
.cv-h6{
  font-size: 0.85rem;
	margin: 18px 9px 0;
	text-transform: uppercase;
}
.cv-powerby{
  font-size: 12px;
  font-weight: 400;
}
.pre-line {
  white-space: pre-line;
}
.coder-box.food {
      box-shadow: 0px 0px 6px 0px rgb(0, 123, 255);
}
.coder-title {
  cursor: pointer;
  margin-bottom: 0;
}
.coder-title:hover:before {
  color: rgb(0, 123, 255);
}
.coder-title:before {
  content: "?";
  padding: 0px 12px 0 0;
  color: rgb(206, 251, 216);
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 1em 0;
  line-height: 11px;
}
.coder-title.active {
  
}
.coder {
  font-size: 14px;
  padding: 10px 20px;
  background-color: rgb(206, 251, 216);
  color: rgb(0, 0, 0);
  opacity: 0;
  height: 0;
}
.active + .coder {
  opacity: 1;
  height: auto;
}

.list-group-item {
  cursor: pointer;
}
.list-group-item.active a {
  color: rgb(255,255,255);
}